.custom-marker {
  display: flex;
  width: 1.5rem;
  height: 1.5rem;
  align-items: center;
  justify-content: center;
  color: #ffffff;
  border-radius: 50%;
  z-index: 1;
}

.bolt {
  display: flex;
  width: 2rem;
  height: 2rem;
  align-items: center;
  justify-content: center;
  color: #ffffff;
  border-radius: 50%;
  z-index: 1;
}

.bolt-status {
  &.succeeded {
    background-color: rgba(85, 163, 98, 1)
  }

  &.failed {
    background-color: rgba(205, 74, 69, 1)
  }
}

label.asterisk-if-mandatory::after {
  content: " *";
  color: red;
}

.textOverflow {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap
}

.align-text-center {
  text-align: center;
}

.align-text-end {
  text-align: end;
}

img {
  max-width: 100%;
  max-height: 100%;
}

.card {
  box-shadow: 0 3px 4px #0000001a, 0 24px 36px #0000000a;
}

.card-without-shadow {
  background: var(--surface-card);
  border: 1px solid var(--surface-border);
  padding:5px !important;
  margin-bottom: 2rem;
  box-shadow: var(--card-shadow);
  border-radius: 6px
}

.task-card {
  background: var(--surface-card);
  padding: 2rem;
  margin-bottom: 2rem;
  box-shadow: var(--card-shadow);
  border-radius: 5px;
  box-shadow: 0 3px 4px #0000001a, 0 24px 36px #0000000a
}

.task-card:hover {
  background-color: var(--surface-ground) !important;
}

.p-sidebar .p-sidebar-header {
  padding: 0;
}

.p-sidebar .p-sidebar-content {
  padding: 0;
}

.p-fileupload-buttonbar {
  justify-content: space-between;
  display: flex;
  align-items: center;
  text-align: center;
}

.p-steps .p-steps-item:before {
  border-top: 0px solid #dee2e6 !important;
}

.p-steps .p-steps-item .p-menuitem-link .p-steps-number {
  border-radius: 17px !important;
}
